.Projects {
  padding: 5rem 0;
  width: 100%;  
}

@media only screen and (min-width: 768px) {
  .Projects {
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
  }

  .Projects-list {
    margin: 0 auto;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    grid-gap: .6rem;
  }
}

@media only screen and (min-width: 1024px) {
  .Projects-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr); 
    grid-gap: .6rem;
  }
}


